<template>
  <div class="wrapper" id="app">
    <div class="contents-wrapper">
      <page-header />
      <router-view/>
      <page-footer />
    </div>
  </div>
</template>

<script>
import PageHeader from './components/PageHeader'
import PageFooter from './components/PageFooter'
export default {
  name: 'App',
  mounted () {
    // WD 要請
    // https://ebookjapan.slack.com/archives/GSW4M6M44/p1599466329312100
    window.onresize = function () {
      const vh = document.documentElement.clientHeight * 0.01
      document.documentElement.style.setProperty('--vh', `${vh}px`)
    }
    window.onresize()
  },
  beforeDestroy () {
    window.onresize = null
  },
  components: {
    PageHeader,
    PageFooter
  }
}
</script>

<style lang="scss">
@import './assets/css/reset.css';
.wrapper {
  padding: 20px 0 30px;
  background: #faf2e5;
  position: relative;
  display: flex;
  justify-content: center;
  min-width: 990px;
  max-width: 100%;
  margin: 0 auto;
  height: calc(var(--vh, 1vh) * 100);
  @media (hover: hover) {
    height: auto;
  }
  .contents-wrapper {
    background: #fff;
    box-shadow: 0 8px 10px 0 rgba(0,0,0,0.2);
    min-height: 350px;
    height: 100%;
    padding-bottom: 56px;  // footerの高さ分空ける
  }
}
</style>



